	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
		height: 50px;
	}

	@-webkit-keyframes App-logo-spin {
		from {
		  -webkit-transform: rotate(0deg);
		          transform: rotate(0deg);
		}
		to {
		  -webkit-transform: rotate(360deg);
		          transform: rotate(360deg);
		}
	}

	@keyframes App-logo-spin {
		from {
		  -webkit-transform: rotate(0deg);
		          transform: rotate(0deg);
		}
		to {
		  -webkit-transform: rotate(360deg);
		          transform: rotate(360deg);
		}
	}

	.App-logo-directional {
		-webkit-animation: App-logo-directional infinite 5s alternate;
		        animation: App-logo-directional infinite 5s alternate;
		height: 50px;
	}

	@-webkit-keyframes App-logo-directional {
		from {
		  -webkit-transform: translate(0);
		          transform: translate(0);
		}
		to {
		  -webkit-transform: translate(100%);
		          transform: translate(100%);
		}
	}

	@keyframes App-logo-directional {
		from {
		  -webkit-transform: translate(0);
		          transform: translate(0);
		}
		to {
		  -webkit-transform: translate(100%);
		          transform: translate(100%);
		}
	}

	.pointeur {
		-webkit-animation: pointeur-directional infinite 1s alternate;
		        animation: pointeur-directional infinite 1s alternate
	}

	@-webkit-keyframes pointeur-directional {
		from {
			-webkit-transform: translate(0);
			        transform: translate(0)
		}
		to{
			-webkit-transform: translate(10px);
			        transform: translate(10px)
		}
	}

	@keyframes pointeur-directional {
		from {
			-webkit-transform: translate(0);
			        transform: translate(0)
		}
		to{
			-webkit-transform: translate(10px);
			        transform: translate(10px)
		}
	}
	  
